/**
 * ===============================================
 * LiveRamp Showcase
 * ===============================================
 * Type
 */

body {
	font-family: @font-face-copy;
	font-weight: 400;
	-webkit-font-smoothing: antialiased;
	-webkit-text-stroke: 0.15px;
}

a {
	color: @color-green;
	text-decoration: none;
	transition: color @link-color-transition-duration linear 0ms;

	&:hover {
		color: @color-blue-dark;
		text-decoration: none;
	}
}

.heading-styles {
	color: @color-green;
	font-weight: 300;
	letter-spacing: -0.06em;
}

h1, .h1 {
	&:extend( .heading-styles );
	font-size: @font-size-heading-1-sm;
	line-height: @line-height-heading-1-sm;
}

h2 {
	&:extend( .heading-styles );
	font-size: @font-size-heading-2-sm;
	line-height: @line-height-heading-2-sm;
}

h3 {
	font-size: @font-size-heading-3-sm;
	line-height: @line-height-heading-3-sm;
	margin: 0;
}

label {
	font-weight: 400;
	margin: 0;
}

p {
	font-size: @font-size-copy-sm;
	line-height: @line-height-copy-sm;

	small {
		display: block;
		font-size: @font-size-small-sm;
		letter-spacing: -0.01em;
		line-height: @line-height-small-sm;
		margin: 0 auto;
	}
}

strong {
	font-weight: 400;
}

ul {
	font-size: @font-size-small-sm;
	letter-spacing: -0.01em;
	line-height: @line-height-small-sm;
}

.cta {
	font-size: @font-size-copy-sm;
	line-height: @line-height-copy-sm;
}

.text-center {
	margin-left: auto;
	margin-right: auto;
	text-align: center;
}

.text-right {
	margin-left: auto;
	margin-right: 0;
	text-align: right;
}

@media ( min-width: @screen-sm ) {
	h1, .h1 {
		font-size: @font-size-heading-1;
		line-height: @line-height-heading-1;
	}

	h2 {
		font-size: @font-size-heading-2;
		line-height: @line-height-heading-2;
	}

	h3 {
		font-size: @font-size-heading-3;
		line-height: @line-height-heading-3;
	}

	p {
		font-size: @font-size-copy;
		line-height: @line-height-copy;

		small {
			font-size: @font-size-small;
			line-height: @line-height-small;
		}
	}

	ul {
		font-size: @font-size-small;
		line-height: @line-height-small;
	}

	.cta {
		font-size: @font-size-copy;
		line-height: @line-height-copy;
	}
}