/**
 * ===============================================
 * LiveRamp Showcase
 * ===============================================
 * Header
 */

#intro {
	padding: 40px 0;

	h1,
	h2,
	p {
		margin-bottom: 20px;
		max-width: 620px;
		opacity: 0;
		transform: translateY(-50%);
		transition: opacity 550ms linear 50ms, transform 750ms @ease-out-circ 50ms;

		.cb-init &,
		.cb-ready & {
			opacity: 1;
			transform: translateY(0);
		}
	}

	h2 {
		margin-bottom: 7px;
	}

	p {
		max-width: 670px;
	}

	small {
		color: @color-green;
	}
}

#topbar {
	background: @color-white;
	font-size: 12px;
	height: 44px;
	left: 0;
	line-height: 14px;
	position: fixed;
	top: 0;
	transform: translateY(-100%);
	transition: background 350ms linear 0ms, transform 750ms @ease-out-circ 600ms;
	width: 100%;
	z-index: 2;

	.cb-init &,
	.cb-ready & {
		transform: translateY(0);
	}

	.fixed & {
		background: @color-blue-light;
	}

	a {
		color: @color-blue-dark;
		display: inline-block;
		margin: 15px 0;
	}

	.btn {
		color: @color-white;
		margin: 8px 0;

		.cb-init & {
			display: none;
		}
	}
}