/**
 * ===============================================
 * LiveRamp Showcase
 * ===============================================
 * Filters
 */

#filters {
	background: @color-white;
	padding: 20px 0;
	transform: scaleY(0);
	transform-origin: 50% 0;
	transition: transform 500ms @ease-out-circ 450ms;
	z-index: 1;

	.cb-init & {
		display: none;
	}

	.cb-init &,
	.cb-ready & {
		transform: scaleY(1);
	}

	.dropdown-col {
		display: block;

		ul, li {
			margin: 0;
			padding: 0;
		}
	}

	.filter-dropdown {
		background: @color-white;
		display: none;
		left: 0;
		margin: 0;
		padding: 0 0 15px;
		position: relative;
		top: 0;
		width: 100%;
		z-index: 1;

		&:after {
			clear: both;
			content: '';
			display: table;
			width: 100%;
		}

		.filter-option {
			box-sizing: border-box;
			font-size: inherit;
			line-height: inherit;
			list-style: none;
			width: 100%;

			&:hover {
				color: @color-green;
			}

			&:last-child {
				padding-bottom: 0;
			}
			
			input {
				display: none;
				&:checked {
					~ .label {
						color: @color-green;
					}

					~ .clear-filter {
						opacity: 1;
						transform: rotateZ(45deg);
					}
				}
			}
			
			label {
				cursor: pointer;
				display: block;
				position: relative;
				transition: color 350ms linear;
			}
			
			span {
				display: inline-block;
			}

			.clear-filter {
				display: inline-block;
				height: 21px;
				opacity: 0;
				text-align: center;
				transform-origin: 10px 11px;
				transition: all @link-color-transition-duration @ease-in-out-circ;
				vertical-align: top;
				width: 21px;

				i {
					color: @color-green;
					display: block;
					font-size: 12px;
					height: 20px;
					line-height: 20px;
					transition: none;

					&:before {
						margin: 0;
					}
				}
			}
		}
	}

	.menu-item {
		opacity: 0;
		transform: translateY(10px);
		transition: all 250ms @ease-in-out-circ;

		.cb-init &,
		.cb-ready & {
			opacity: 1;
			transform: translateY(0);
		}

		&:nth-child(1) {
			transition-delay: 750ms;
		}

		&:nth-child(2) {
			transition-delay: 850ms;
		}

		&:nth-child(3) {
			transition-delay: 950ms;
		}
	}

	.active-filters {
		.btn-clear {
			opacity: 1;
			transform: translateX(0);

			&:hover {
				i {
					transform: rotateZ(225deg);
				}
			}

			i {
				transform: rotateZ(45deg);
			}
		}
	}

	.filter-group-heading {
		cursor: pointer;

		h3 {
			border-top: 2px solid @color-grey-mid;
			color: @color-green;
			padding: 10px 0;
			position: relative;

			.label {
				display: block;
			}

			.btn-clear {
				z-index: 1;
			}

			.icon-chevron {
				display: inline-block;
				position: relative;
				transform: rotateZ(0);
				transform-origin: 45% 55%;
				transition: transform @link-color-transition-duration @ease-in-out-circ;
			}

			.label {
				width: ~"calc(100% - 40px)";
			}
		}
	}

	.filters-label {
		&:extend( h3 all );
		color: @color-blue-dark;
		display: block;
		margin: 0;
		padding: 10px 0;
		text-align: left;
	}

	.filters-open {
		h3 {
			.icon-chevron {
				transform: rotateZ(90deg);
			}
		}

		~ .filter-options {
			.filter-dropdown {
				display: block;
			}
		}
	}
}

@media ( min-width: @screen-md ) {
	#filters {
		.filter-dropdown {
			padding: 5px 7px 15px;
			position: absolute;
			min-width: 455px;
			
			.filter-option {
				padding: 0 7px 5px;
				width: 220px;
			}

			.dropdown-col {
				float: left;
			}
		}

		.col-md-4:hover {
			.filter-dropdown {
				display: block !important;
			}
		}

		.filter-group-heading {
			h3 {
				width: 85%;
			}
		}

		.filters-label {
			text-align: right;
		}

		.filters-open ~ .filter-options {
			.filter-dropdown {
				display: none;
			}
		}
	}
}

@media ( min-width: @screen-md ) {
	#filters {
		.filter-group-heading {
			h3 {
				.icon-chevron {
					display: none;
				}
			}
		}
	}
}