/**
 * ===============================================
 * LiveRamp Showcase
 * ===============================================
 * Forms
 */

/* General form styles */
#marketo-form {
	background: fade( @color-black, 75% );
	display: block !important;
	height: 100%;
	left: 0;
	opacity: 1;
	-webkit-overflow-scrolling: touch;
	overflow: scroll;
	position: fixed;
	top: 0;
	transform: translateY(0) scale(1);
	transition: opacity 500ms linear 0ms, transform 0ms linear 0ms;
	width: 100%;
	z-index: 3;

	&.hidden {
		opacity: 0;
		overflow: hidden;
		transform: translateY(-100%) scale(0);
		transition-delay: 0ms, 500ms;
	}

	h2 {
		margin: 0 0 40px;
		text-align: left;
	}

	.form-container {
		display: table;
		height: 100%;
		left: 0;
		position: absolute;
		right: 0;
		table-layout: fixed;
		top: 0;
		width: 100%;

		.form-wrapper {
			display: table-cell;
			text-align: center;
			vertical-align: middle;

			.form-content {
				background: @color-white;
				border-radius: 6px;
				box-shadow: 0 0 15px fade( @color-black, 25% );
				display: inline-block;
				max-width: 680px;
				padding: 20px;
				width: 100%;

				.form-success {
					display: none;
				}
			}
		}
	}
}

/* Marketo Overrides */
#marketo-form {
	.mktoForm {
		font-family: @font-face-copy !important;
		padding: 0;
		width: 100% !important;

		.mktoButtonRow {
			display: block;
			margin-top: 15px;
			text-align: center;

			.mktoButtonWrap {
				margin: auto !important;
				text-align: center;

				.mktoButton {
					&:extend( .btn-primary all );
					line-height: inherit;
				}
			}
		}

		.mktoErrorArrow {
			background: @color-green;
			border-color: @color-green;
		}

		.mktoErrorMsg {
			background: @color-green;
			border-color: @color-green;
			box-shadow: none;
			padding: 6px 8px;
			text-shadow: none;
		}

		.mktoFormRow {
			.mktoFieldWrap {
				float: none;

				input {
					background: none !important;
					border: 1px solid @color-blue;
					border-radius: 6px;
					box-shadow: none;
					font-size: @font-size-copy-sm;
					line-height: @line-height-copy-sm;
					width: 100% !important;
				}

				.mktoLabel {
					font-weight: normal;
					margin-bottom: 6px;
					width: 100% !important;
				}
			}

			.mktoAsterix,
			.mktoGutter,
			.mktoOffset {
				display: none;
			}
		}

		.mktoFormCol {
			width: 100%;
		}
	}
}

@media ( min-width: @screen-sm ) {
	#marketo-form {
		.form-container {
			.form-wrapper {
				.form-content {
					padding: 40px;
					> div {
						border: 4px solid @color-green;
						border-radius: 6px;
						padding: 60px;
					}
				}
			}
		}
		.mktoForm {
			padding: 10px;

			.mktoFormRow {
				.mktoFieldWrap {
					input {
						line-height: @line-height-copy;
					}
				}
			}
		}
	}
}