/**
 * ===============================================
 * LiveRamp Showcase
 * ===============================================
 * Case Studies
 */
#case-studies {
  overflow: hidden;
  padding: 10px 0;
  vertical-align: middle;
  width: 100%;
}
#case-studies .case-study {
  left: 0;
  overflow: hidden;
  position: absolute;
  top: 0;
  visibility: hidden;
}
.cb-init #case-studies .case-study {
  position: relative;
  transform: none;
  visibility: visible;
}
#case-studies .case-study.visible {
  position: relative;
  visibility: visible;
}
#case-studies .case-study h2 {
  box-sizing: border-box;
  margin-bottom: 40px;
  margin-top: 0;
  opacity: 0;
  padding-top: 30px;
  transform: translateY(50%);
  transition: opacity 0ms linear 1500ms, transform 0ms cubic-bezier(0.075, 0.82, 0.165, 1) 1500ms;
}
.cb-init #case-studies .case-study h2,
.fixed #case-studies .case-study h2 {
  opacity: 1;
  transform: translateY(0);
  transition-delay: 1050ms, 1050ms;
  transition-duration: 1500ms, 1250ms;
}
#case-studies .case-study p {
  margin-bottom: 30px;
  opacity: 0;
  transform: translateY(50%);
  transition: opacity 0ms linear 1500ms, transform 0ms cubic-bezier(0.075, 0.82, 0.165, 1) 1500ms;
}
.cb-init #case-studies .case-study p,
.fixed #case-studies .case-study p {
  opacity: 1;
  transform: translateY(0);
  transition-delay: 0ms, 0ms;
  transition-duration: 1250ms, 1000ms;
}
#case-studies .case-study p:nth-child( 7) {
  transition-delay: 1925ms, 1925ms;
}
#case-studies .case-study p:nth-child( 6) {
  transition-delay: 1800ms, 1800ms;
}
#case-studies .case-study p:nth-child( 5) {
  transition-delay: 1675ms, 1675ms;
}
#case-studies .case-study p:nth-child( 4) {
  transition-delay: 1550ms, 1550ms;
}
#case-studies .case-study p:nth-child( 3) {
  transition-delay: 1425ms, 1425ms;
}
#case-studies .case-study p:nth-child( 2) {
  transition-delay: 1300ms, 1300ms;
}
#case-studies .case-study p:nth-child( 1) {
  transition-delay: 1175ms, 1175ms;
}
#case-studies .case-study .btn-close {
  margin: 20px 0 0;
  opacity: 0;
  transform: translateX(50%);
  transition: background 175ms linear 0ms, color 175ms linear 0ms, opacity 250ms cubic-bezier(0.785, 0.135, 0.15, 0.86) 1925ms, transform 250ms cubic-bezier(0.785, 0.135, 0.15, 0.86) 1925ms;
}
.cb-init #case-studies .case-study .btn-close {
  display: none;
}
.fixed #case-studies .case-study .btn-close {
  opacity: 1;
  transform: translateX(0);
}
#case-studies .case-study .btn-close.btn-visible {
  margin: 0 auto 20px;
  position: relative;
}
#case-studies .case-study .btn-close i {
  transform: rotateZ(0);
  transition: all 350ms linear 0ms, color 0ms linear 0ms, transform 350ms linear 1925ms;
}
.fixed #case-studies .case-study .btn-close i {
  transform: rotateZ(135deg);
}
@media (min-width: 992px) {
  #case-studies {
    display: table-cell;
    padding: 40px;
  }
  #case-studies .case-study:after,
  #case-studies .case-study:before {
    content: '';
    display: block;
    height: 4px;
    left: 0;
    position: absolute;
    transform-origin: 50% 50%;
    transition: transform 350ms cubic-bezier(0.6, 0.04, 0.98, 0.335) 650ms;
    width: 100%;
  }
  #case-studies .case-study:after {
    background: #ffffff;
    bottom: 0;
    transform: scaleX(1);
    transition-delay: 1350ms;
    transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
  }
  .fixed #case-studies .case-study:after {
    transform: scaleX(0);
  }
  #case-studies .case-study:before {
    background: #73c167;
    top: 0;
    transform: scaleX(0);
  }
  .fixed #case-studies .case-study:before {
    transform: scaleX(1);
  }
  #case-studies .case-study h2 {
    max-width: 740px;
    padding-right: 100px;
    padding-top: 0;
  }
  #case-studies .case-study .btn-close {
