  .fixed #case-studies .case-study .case-study-content:before {
    transform: scaleY(1);
  }
  #case-studies .case-study .case-study-content:after {
    right: 0;
  }
  #case-studies .case-study .case-study-content:before {
    left: 0;
  }
}
