  background: #009ee2;
  transform: scale(1.25);
}
.social-media .social-link:hover i {
  transform: scale(1.5);
}
.social-media .social-link:nth-child(2) {
  transform: translateX(-150%);
}
.social-media .social-link:nth-child(3) {
  transform: translateX(-300%);
}
.social-media .social-link:nth-child(4) {
  transform: translateX(-450%);
}
.fixed .social-media .social-link:nth-child(1),
.fixed .social-media .social-link:nth-child(2),
.fixed .social-media .social-link:nth-child(3),
.fixed .social-media .social-link:nth-child(4) {
  opacity: 1;
  transform: translateX(0);
}
.social-media .social-link i {
  color: #ffffff;
  display: block;
  position: relative;
  text-align: center;
  transition: transform 175ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
  transform-origin: 50% 50%;
  white-space: nowrap;
  z-index: 2;
}
.social-media .social-link i:before {
  margin: 0;
}
.social-media .social-link span {
  display: none;
  white-space: nowrap;
}
@media (min-width: 992px) {
  .social-media {
    text-align: left;
  }
}
