/**
 * ===============================================
 * LiveRamp Showcase
 * ===============================================
 * Case Studies
 */

@iterations: 7;

#case-studies {
	overflow: hidden;
	padding: 10px 0;
	vertical-align: middle;
	width: 100%;
	
	.case-study {
		left: 0;
		overflow: hidden;
		position: absolute;
		top: 0;		
		visibility: hidden;

		.cb-init & {
			position: relative;
			transform: none;
			visibility: visible;
		}

		&.visible {
			position: relative;
			visibility: visible;
		}

		h2 {
			box-sizing: border-box;
			margin-bottom: 40px;
			margin-top: 0;
			opacity: 0;
			padding-top: 30px;
			transform: translateY(50%);
			transition: opacity 0ms linear @screen-wipes-visibility-offset, transform 0ms @ease-out-circ @screen-wipes-visibility-offset;

			.cb-init &,
			.fixed & {
				opacity: 1;
				transform: translateY(0);
				transition-delay: @case-study-content-title-offset, @case-study-content-title-offset;
				transition-duration: 1500ms, 1250ms;
			}
		}

		p {
			margin-bottom: 30px;
			opacity: 0;
			transform: translateY(50%);
			transition: opacity 0ms linear @screen-wipes-visibility-offset, transform 0ms @ease-out-circ @screen-wipes-visibility-offset;

			.cb-init &,
			.fixed & {
				opacity: 1;
				transform: translateY(0);
				transition-delay: 0ms, 0ms;
				transition-duration: 1250ms, 1000ms;
			}
			
			.content-element( @i ) when ( @i > 0 ) {
				&:nth-child( @{i} ) {
					transition-delay: ~"@{case-study-content-@{i}-offset}", ~"@{case-study-content-@{i}-offset}";
				}
				
				.content-element( @i - 1 );
			}			
			.content-element( @iterations );
		}

		.btn-close {
			margin: 20px 0 0;
			opacity: 0;
			transform: translateX(50%);
			transition: background @link-color-transition-duration linear 0ms, color @link-color-transition-duration linear 0ms, opacity 250ms @ease-in-out-circ ~"@{case-study-content-@{iterations}-offset}", transform 250ms @ease-in-out-circ ~"@{case-study-content-@{iterations}-offset}";

			.cb-init & {
				display: none;
			}

			.fixed & {
				opacity: 1;
				transform: translateX(0);
			}

			&.btn-visible {
				margin: 0 auto 20px;
				position: relative;
			}

			i {
				transform: rotateZ(0);
				transition: all 350ms linear 0ms, color 0ms linear 0ms, transform 350ms linear ~"@{case-study-content-@{iterations}-offset}";

				.fixed & {
					transform: rotateZ(135deg);
				}
			}
		}
	}
}

@media ( min-width: @screen-md ) {
	#case-studies {
		display: table-cell;
		padding: 40px;

		.case-study {
			&:after,
			&:before {
				content: '';
				display: block;
				height: 4px;
				left: 0;
				position: absolute;
				transform-origin: 50% 50%;
				transition: transform 350ms @ease-in-circ 650ms;
				width: 100%;
			}

			&:after {
				background: @color-white;
				bottom: 0;
				transform: scaleX(1);
				transition-delay: 1350ms;
				transition-timing-function: @ease-out-circ;

				.fixed &  {
					transform: scaleX(0);
				}
			}

			&:before {
				background: @color-green;
				top: 0;
				transform: scaleX(0);

				.fixed &  {
					transform: scaleX(1);
				}
			}

			h2 {
				max-width: 740px;
				padding-right: 100px;
				padding-top: 0;
			}

			.btn-close {
				margin: 0;
			}

			.case-study-content {
				border-bottom: 4px solid @color-green;
				padding: 40px;

				&:after,
				&:before {
					background: @color-green;
					content: '';
					display: block;
					height: 100%;
					position: absolute;
					top: 0;
					transform: scaleY(0);
					transform-origin: 50% 0;
					transition: transform 350ms linear 1000ms;
					width: 4px;

					.fixed & {
						transform: scaleY(1);
					}
				}

				&:after {
					right: 0;
				}

				&:before {
					left: 0;
				}
			}
		}
	}
}