/**
 * ===============================================
 * LiveRamp Showcase
 * ===============================================
 * Case Study Images
 */

.case-study-images {
	margin: auto;
	max-width: 480px;

	span {
		display: block;
		height: 100%;
		left: 0;
		opacity: 0;
		overflow: hidden;
		position: absolute;
		top: 0;
		transition-delay: 975ms, 975ms;
		transition-property: opacity, transform;
		transition-timing-fuction: linear, @ease-out-circ;
		white-space: nowrap;
		width: 100%;

		&:before {
			background: @color-white;
			content: '';
			display: block;
			height: 100%;
			left: 0;
			position: absolute;
			top: 0;
			transform: scaleY(0);
			transform-origin: 50% 100%;
			transition-delay: 975ms;
			transition-property: transform;
			transition-timing-function: @ease-out-expo;
			width: 100%;
		}

		&:first-child {
			position: relative;
		}

		&:nth-child(1) {
			transform: translateX(70%);
			transition-delay: 1310ms, 1310ms;
			transition-duration: 750ms, 750ms;
			z-index: 5;

			&:before {
				transition-duration: 1050ms;
			}
		}

		&:nth-child(2) {
			transform: translateX(70%);
			transition-delay: 1180ms, 1180ms;
			transition-duration: 750ms, 750ms;
			z-index: 4;

			&:before {
				transition-duration: 950ms;
			}
		}

		&:nth-child(3) {
			transform: translateX(70%);
			transition-delay: 1167ms, 1167ms;
			transition-duration: 750ms, 750ms;
			z-index: 3;

			&:before {
				transition-duration: 850ms;
			}
		}

		&:nth-child(4) {
			transform: translateX(70%);
			transition-delay: 1150ms, 1150ms;
			transition-duration: 750ms, 750ms;
			z-index: 2;

			&:before {
				transition-duration: 750ms;
			}
		}

		&:nth-child(5) {
			transform: translateX(70%);
			transition-delay: 975ms, 975ms;
			transition-duration: 500ms, 500ms;
			z-index: 1;

			&:before {
				transition-duration: 500ms;
			}
		}

		&:nth-child(1n) {
			.cb-init &,
			.fixed & {
				opacity: 1;
				transform: translateX(0);

				&:before {
					transform: scaleY(0);
				}
			}
		}

		img {
			display: block;
			height: auto;
			width: 100%;
		}
	}
}

@media ( min-width: @screen-md ) {
	.case-study-images {
		max-width: none;
	}
}