/**
 * ===============================================
 * LiveRamp Showcase
 * ===============================================
 * Layout
 */

html {
	height: 100%;
	width: 100%;
}

body {
	background: @color-blue-light;
	background-image: url( '../images/bg-tile.png' );
	background-position: 0 0;
	background-repeat: repeat;
	background-size: 30px auto;
	height: 100%;
	max-width: 100vw;
	overflow: hidden;
	position: relative;
	width: 100%;

	&.cb-ready {
		overflow: auto;
	}

	&.cb-init {
		height: auto;
		overflow: auto;
	}

	&.fixed {
		overflow: hidden;
	}
}

div {
	box-sizing: border-box;
	position: relative;
}

#browser {
	left: 0;
	overflow: hidden;
	padding-top: 44px;
	position: relative;
	top: 0;
	width: 100%;
}

#content {
	background: @color-white;
	height: 100%;
	left: 0;
	-webkit-overflow-scrolling: touch;
	overflow: auto;
	padding-top: 44px;
	position: fixed;
	top: 0;
	transform: translateY(100%);
	transition: transform 650ms @ease-in-out-circ 0ms;
	width: 100%;
	z-index: 1;

	.cb-init & {
		position: relative;
		transform: none;
	}

	.fixed & {
		transform: translateY(0);
	}

	.container {
		height: 100%;
		table-layout: fixed;
	}
}

.logo {
	margin-bottom: 10px;
	max-width: 136px;
}

@media ( min-width: @screen-md ) {
	#content {
		.container {
			display: table;
		}
	}
}