/**
 * ===============================================
 * LiveRamp Showcase
 * ===============================================
 * CTAs & Buttons
 */

.btn {
	box-shadow: none;
	display: inline-block;
	font-size: inherit;
	line-height: inherit;
	position: relative;
	text-decoration: none;
	text-shadow: none;
	width: auto;

	&:hover {
		text-decoration: none;
	}

	i {
		display: block;
		font-size: 32px;
		height: 32px;
		line-height: 32px;
		margin-top: -16px;
		position: absolute;
		right: 0;
		text-align: center;
		text-decoration: none;
		top: 50%;
		transform-origin: 50% 50%;
		width: 32px;
		
		&:before {
			display: block;
			height: 100%;
			font-size: inherit;
			line-height: 100%;
			margin: 0;
			text-align: center;
			width: 100%;
		}
	}
}

.btn-clear {
	&:extend( .btn all );

	background: @color-white;
	border: 1px solid @color-green;
	border-radius: 100px;
	box-sizing: border-box;
	height: 24px;
	margin-top: -12px;
	opacity: 0;
	padding: 6px 22px 6px 8px;
	position: absolute;
	right: 0;
	transform: translateX(20px);
	transform-origin: 50% 50%;
	transition: all @link-color-transition-duration linear 0ms, transform @link-color-transition-duration @ease-in-out-quad 0ms;
	top: 50%;

	&:hover {
		background: @color-green;
		color: @color-white;
	}

	i {
		font-size: 10px;
		height: 12px;
		line-height: 12px;
		margin-top: -5px;
		right: 6px;
		transform-origin: 6px 5px;
		transition: transform 75ms linear 0ms;
		width: 12px;

		&:before {
			transition: none;
		}
	}

	.btn-label {
		display: inline-block;
		font-size: 12px;
		height: 12px;
		line-height: 12px;
		vertical-align: top;
	}
}

.btn-close {
	height: 32px;
	opacity: 1;
	overflow: hidden;
	padding: 6px 34px 6px 16px;
	transform: none;
	white-space: nowrap;

	&:hover {
		i {
			&:before {
				transform: rotateZ(90deg);
			}
		}
	}

	i {
		font-size: 14px;
		height: 14px;
		line-height: 24px;
		margin-top: -7px;
		right: 6px;
		transform-origin: 50% 50%;
		transform: rotateZ(45deg);
		width: 24px;

		&:before {
			transform-origin: 50% 50%;
			transition: transform @link-color-transition-duration linear 0ms;
		}
	}

	.label {
		font-size: 16px;
		height: 20px;
		line-height: 20px;
	}
}

.btn-primary {
	&:extend( .btn all );
	background: @color-orange;
	border: none;
	border-radius: 6px;
	color: @color-white;
	font-weight: 400;
	line-height: 12px;
	padding: 8px 12px;
	margin: 5px 0;
	transition: background @link-color-transition-duration linear;
	
	&:hover {
		background: @color-blue-dark;
	}
}

.btn-secondary {
	&:extend( h3 all );
	border: 2px solid @color-green;
	border-radius: 6px;
	color: @color-green;
	display: block;
	margin: 0;
	padding: 10px 12px;
	text-align: center;
	transition: border @link-color-transition-duration linear, color @link-color-transition-duration linear;
	width: 100%;

	&:hover {
		border-color: @color-orange;
		color: @color-orange;
	}
}

@media ( min-width: @screen-sm ) {
	.btn-close {
		&:hover {
			.label {
				opacity: 1;
				transform: translateX(0);
			}
		}
	}

	.btn-primary {
		padding: 8px 24px;
	}
}

@media ( min-width: @screen-md ) {
	.btn-clear {
		.label-sub {
			display: none;
		}
	}
}

@media ( min-width: @screen-lg ) {
	.btn-clear {
		.label-sub {
			display: inline-block;
		}
	}
}