/**
 * ===============================================
 * LiveRamp Showcase
 * ===============================================
 * Social
 */

.social-media {
	font-size: 0;
	line-height: 0;
	margin: 20px 0;
	text-align: center;

	.social-link {
		border-radius: 100%;
		color: @color-white;
		display: inline-block;
		font-size: 18px;
		line-height: 30px;
		height: 32px;
		margin-right: 16px;
		opacity: 0;
		position: relative;
		text-align: center;
		transform-origin: 50% 50%;
		transition: opacity @social-media-transition-duration @ease-out-circ @social-media-transition-offset, transform @social-media-transition-duration @ease-out-circ @social-media-transition-offset;
		vertical-align: top;
		white-space: nowrap;
		width: 32px;

		&:before {
			background: @color-grey-mid;
			border-radius: 100%;
			content: '';
			display: block;
			height: 100%;
			left: 0;
			overflow: hidden;
			position: absolute;
			top: 0;
			transition: background 125ms linear, transform 125ms @ease-out-quad;
			transform-origin: 50% 50%;
			width: 100%;
			z-index: 1;
		}

		&:hover {
			&:before {
				background: @color-blue;
				transform: scale(1.25);
			}

			i {
				transform: scale(1.5);
			}
		}

		&:nth-child(1) {

		}

		&:nth-child(2) {
			transform: translateX(-150%);
		}

		&:nth-child(3) {
			transform: translateX(-300%);
		}

		&:nth-child(4) {
			transform: translateX(-450%);
		}

		&:nth-child(1),
		&:nth-child(2),
		&:nth-child(3),
		&:nth-child(4) {
			.fixed & {
				opacity: 1;
				transform: translateX(0);
			}
		}

		i {
			color: @color-white;
			display: block;
			position: relative;
			text-align: center;
			transition: transform 175ms @ease-out-quad;
			transform-origin: 50% 50%;
			white-space: nowrap;
			z-index: 2;

			&:before {
				margin: 0;
			}
		}

		span {
			display: none;
			white-space: nowrap;
		}
	}
}

@media ( min-width: @screen-md ) {
	.social-media {
		text-align: left;
	}
}