/**
 * Icon font
 *
 * IF YOU ARE CHANGING THIS FILE MAKE SURE YOU ARE CHANGING THE TEMPLATE NOT THE GENERATED FILE
 */


@font-face {
    font-family: 'iconfont';
    src:url('../fonts/iconfont.eot');
    src:url('../fonts/iconfont.eot') format('embedded-opentype'),
    url('../fonts/iconfont.ttf') format('truetype'),
    url('../fonts/iconfont.woff') format('woff'),
    url('../fonts/iconfont.svg') format('svg');
    font-weight: normal;
    font-style: normal;
}


.icon:before,
[class*="icon-"]:before{
    display: inline-block;
    font-family: "iconfont";
    font-style: normal;
    font-weight: normal;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    vertical-align: middle;
    font-size: 1em;
    color: currentColor;
    transition: all 0.5s;
    margin-right: 5px;
}

// Icon sizes
.icon-sm:before{ font-size: 0.5em; }
.icon-md:before{ font-size: 1.5em; }
.icon-lg:before{ font-size: 2em; }
.icon-xl:before{ font-size: 5em; }


// Icon colors
.icon-c-primary:before{ color: @brand-primary; }
.icon-c-success:before{ color: @brand-success; }
.icon-c-danger:before{ color: @brand-danger; }

// Top layer (tl) icon colors
.icon-c-tl-primary:after{ color: @brand-primary; }
.icon-c-tl-success:after{ color: @brand-success; }
.icon-c-tl-danger:after{ color: @brand-danger; }

// Hover colors
:hover{
    > .icon-c-hover-primary:before{ color: @brand-primary;  }
    > .icon-c-hover-success:before{ color: @brand-success; }
    > .icon-c-hover-danger:before{ color: @brand-danger; }
}

// Top layer (tl) hover colors
:hover{
    > .icon-hover-c-tl-primary:after{ color: @brand-primary;  }
    > .icon-hover-c-tl-success:after{ color: @brand-success; }
    > .icon-hover-c-tl-danger:after{ color: @brand-danger; }
}


// Hover on self - for when extending
.icon-hover-self-c-primary:hover:before{ color: @brand-primary;  }
.icon-hover-self-c-success:hover:before{ color: @brand-success; }
.icon-hover-self-c-danger:hover:before{ color: @brand-danger; }

// Hover on self top level
.icon-hover-self-c-tl-primary:hover:after{ color: @brand-primary;  }
.icon-hover-self-c-tl-success:hover:after{ color: @brand-success; }
.icon-hover-self-c-tl-danger:hover:after{ color: @brand-danger; }



// Hover rotation
:hover{
    > .icon-hover-rotate-90:before{ transform: rotate(90deg); }
    > .icon-hover-rotate-180:before{ transform: rotate(180deg); }
    > .icon-hover-rotate-270:before{ transform: rotate(270deg); }
    > .icon-hover-rotate-360:before{ transform: rotate(360deg); }
}

// Top layer (tl) rotation
:hover{
    > .icon-hover-rotate-tl-90:before{ transform: rotate(90deg); }
    > .icon-hover-rotate-tl-180:before{ transform: rotate(180deg); }
    > .icon-hover-rotate-tl-270:before{ transform: rotate(270deg); }
    > .icon-hover-rotate-tl-360:before{ transform: rotate(360deg); }
}

// Hover rotation
.icon-hover-self-rotate-90:hover:before{ transform: rotate(90deg); }
.icon-hover-self-rotate-180:hover:before{ transform: rotate(180deg); }
.icon-hover-self-rotate-270:hover:before{ transform: rotate(270deg); }
.icon-hover-self-rotate-360:hover:before{ transform: rotate(360deg); }

// Top layer (tl) rotation
.icon-hover-self-rotate-tl-90:hover:before{ transform: rotate(90deg); }
.icon-hover-self-rotate-tl-180:hover:before{ transform: rotate(180deg); }
.icon-hover-self-rotate-tl-270:hover:before{ transform: rotate(270deg); }
.icon-hover-self-rotate-tl-360:hover:before{ transform: rotate(360deg); }


// Icon pad
.icon-no-pad:before,
.icon-no-pad:after{
    margin-right: 0;
}


// Icons with a second layer
.icon-has-top-layer{
    position: relative;

    &:after{
        &:extend(.icon:before);
        &:extend(.icon-absolute);
    }
}

// Position icon absolutely - important is added as this should always override for multilayer icons
.icon-absolute{
    position: absolute !important;
    left: 0;
}

// Icon sizes
.icon-sm:after{ font-size: 0.5em; }
.icon-md:after{ font-size: 1.5em; }
.icon-lg:after{ font-size: 2em; }
.icon-xl:after{ font-size: 5em; }


// Glyph classes


.icon-chevron:before {
    content: "\E001"
}



.icon-close:before {
    content: "\E002"
}



.icon-facebook:before {
    content: "\E003"
}



.icon-googleplus:before {
    content: "\E004"
}



.icon-linkedin:before {
    content: "\E005"
}



.icon-twitter:before {
    content: "\E006"
}



.icon-plus:before {
    content: "\E007"
}

