/**
 * ===============================================
 * LiveRamp Showcase
 * ===============================================
 * Grid
 */

#grid-container {
	position: relative;
	width: 100%;

	&:after {
		content: '';
		clear: both;
		display: block;
	}

	&.active-filters {
		.grid-item {
			opacity: 0.3;
			
			&.filter-active {
				opacity: 1;
			}
		}
	}
	
	.grid-item {
		cursor: pointer;
		float: left;
		height: 440px;
		opacity: 1;
		position: relative;
		transition: all 350ms @ease-in-out-circ, visibility 0ms linear;
		width: 100%;

		a {
			background: @color-blue;
			color: @color-white;
			display: block;
			height: ~"calc(100% - 2px)";
			left: 1px;
			opacity: 0;
			overflow: hidden;
			position: absolute;
			text-decoration: none;
			top: 1px;
			transform: translateY(15%);
			transition: all 350ms @ease-out-circ;
			width: ~"calc(100% - 2px)";

			.cb-init &,
			.cb-ready & {
				opacity: 1;
				transform: translateY(0);
			}

			.fixed & {
				opacity: 0;
				transform: translateY(15%);
				transition-delay: 3s;
			}

			&:after,
			&:before {
				background-position: 50% 50%;
				background-repeat: no-repeat;
				background-size: cover;
				content: '';
				display: block;
				height: 100%;
				left: 0;
				position: absolute;
				top: 0;
				width: 100%;
			}

			&:after {
				opacity: 1;
				transition: opacity 250ms linear;
			}

			&:hover {
				&:after {
					opacity: 0;
				}

				.grid-item-card {
					transform: translateX(-100%);
				}
			}

			&.case-study-idil {
				&:after {
					background-image: url( '../images/idil-off.jpg' );
				}

				&:before {
					background-image: url( '../images/idil-on.jpg' );
				}

				.illustration {
