		h2 {
			color: @color-white;
			margin: 0 0 20px;
		}

		.cta {
			&:extend( small all );
		}

		.grid-item-card {
			height: 100%;
			transition: transform 350ms @ease-in-out-circ;
			z-index: 1;
		}

		.tile {
			box-sizing: border-box;
			height: 100%;
			left: 0;
			padding: 25px;
			position: absolute;
			top: 0;
			width: 100%;
			
			&.excerpt {
				left: 100%;

				.cta {
					color: @color-grey-mid;
					text-decoration: underline;
				}
			}

			.illustration {
				background-position: 50% 50%;
				background-repeat: no-repeat;
				background-size: 100% auto;
				bottom: 25px;
				display: block;
				height: 120px;
				left: 25px;
				position: absolute;
				width: 120px;
			}
		}
	}

	.grid-wrapper {
		height: 100%;
		left: -1px;
		position: relative;
		width: ~"calc(100% + 2px)";
	}
}

@media ( min-width: @screen-sm ) {
	#grid-container {
		.grid-item {
			width: 50%;
		}
	}
}

@media ( min-width: @screen-md ) {
	#grid-container {
		.grid-item {
			width: 25%;
		}
	}
}

